// institute-overview

.institute-overview {
	@extend %section-margin-base;
    .section-header {margin:0 0 rem(35px);}
    .overview-title {
    	width:100vw;
    	height:calc(100vw * 340 / 640);
    	background-size:cover;
        background-image: url('../img/institute/bg_overview.jpg');
		.sub-title {
			color:$text-color-gray;
            position:relative;
            display:inline-block;
            width:auto;
            margin-bottom:rem(20px);
            &:after {
                content:'';
                background-color:$institute-text-color;
                height:rem(3px);
                width:100%;
                display:inline-block;
            }
        }
	}
	.overview_img_box {
		margin:rem(30px) 0 rem(20px);
		width:100vw;
		height:auto;
		.overview_img {background-size:cover;background-position:100% 50%;background-image:url('../img/institute/overview_img.jpg');background-repeat:no-repeat;width:100vw;height:calc(100vw * 400 / 640);display:block;}
			display:inline-block;
			padding:rem(20px) 0 0;
			width:100%;
			font-size:rem(13px);
			color:$text-color-gray;
		p.ref {color:$text-color-dgray;text-align:center;margin:rem(12.5px) 0 0;}
	}
	.box-btn {
	    position:relative;
	    height:calc(100vw * 340 / 640);
	    width:100vw;
	    background-color:#282832;
    .box-inner {
        position:relative;
        margin:0 auto;
        width:100vw;
        padding:rem(50px) 0 rem(30px);
        text-align:center;
        span {color:#fff;display:block;line-height:1;
            &.title {font-size:rem(26px);font-weight:500;}
            &.sub-title {font-size:rem(13px);margin-bottom:rem(20px);margin:rem(15px) 0;}
            &.arrow-right {
                    height:rem(20px);
                    width:rem(37px);
                    display:block;
                    background-size:cover;
                    margin:0 auto;
                    background-image:url('data:img/png;base64,');
    		}
  
        }
    }
}
	#infoGraphic {
		@extend %section-margin-base;	
		background:$institute-bg-color;
		padding:rem(40px) 0  rem(35px);
		.infoGraphic {
			padding: rem(35px) 0;
			span {
				color:$text-color-gray;
				display:block; 
				background-size:100% 100%;
			    -o-background-size: 100% 100%;
			    -webkit-background-size: 100% 100%;
			    background-size:cover;
			    margin:0 auto;
			}
			.countUp {font-size:rem(65px);font-weight:600;display:block;line-height:1;margin:rem(15px) 0 rem(10px);
				i {font-size:rem(15px);font-family:$font-family;font-style:normal;display:block;margin-bottom:rem(15px);}
				&:after {
					content:'';
					background:$text-color-gray;
					height:1px;
					width:50%;
					display:block;
					margin:0 auto;
				}
			}
			.info_desc {line-height:1.6;position:relative;
				&:before {
					content:'';
					background-color:#fff;
					height:1px;
					width:100%;
					display:inline-block;
				}
				&:after {
				    position: absolute;
				    top: -19px;
				    width:100%;
				    content: '\00b7';
				    text-align: center;
				    font-size: 50px;
				    width: 10px;
				    left: calc(50% - 5px);
				}
			}
			.infocontents {width:100%;display:inline-block;font-weight:400;}
			.infoSVG {position:relative;
				span {margin:0 auto;display:block;}
			}
			.infoG1 {width:rem(140px);height:rem(114px);background-image:url('data:img/png;base64,');}
			.infoG2 {width:rem(120px);height:rem(118px);background-image:url('data:img/png;base64,');}
			.infoG3 {width:rem(150px);height:rem(91px);background-image:url('data:img/png;base64,');}		.infoG4 {background-image:url('data:img/png;base64,');}
			.infoG4 {width:rem(140px);height:rem(113px);background-image:url('data:img/png;base64,');}
		}
	}
}