$sp-img : url('../img/institute/sprite.png');
.institute, .institute-overview, .institute-curriculum, .institute-remark, .institute-overview, .institute-interview, .institute-qa {
    text-align:center;
    .owl-prev, .owl-next {
    background:none;
        &:before {content:none;}
}
    p.al {color:$text-color-dgray;text-align:left;}
    .article {border:none;font-weight:300;}
}
.institute-overview, .institute-curriculum {
    padding:0;
    .section-header {
        position: relative;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        width:100%;
        height: 260px;
        margin-top: rem($vertical-margin-1);
        background-size: contain;
        background-position: 100% 50%;
        background-repeat: no-repeat;
        background-color:$institute-header-color;
        padding:0;
        .heading-1, .title, .sub-title {color: $text-color-gray;}
        .sub-title {font-weight:400;}
    }
    blockquote {
      margin: rem(15px) 0;
      position: relative;
      font-size:rem(19px);
      font-weight:500;
      line-height:1.4;
      &:before,
      &:after {
        position: relative;
        display: inline-block;
        width: rem(15px);
        height: rem(10px);
        vertical-align: baseline;
        background-repeat: no-repeat;
        background-size: cover;
        margin:0;
      }
      &:before {
        content: '';
        top: rem(-0px);
        background-image: url('data:img/png;base64,');
        background-repeat:no-repeat;
      }
      &:after {
        content: '';
        top: rem(0px);
        background-image: url('data:img/png;base64,');
        background-repeat:no-repeat;    
        }
    }
}
.prev-circle-arrow, .next-circle-arrow, .prev-interview-arrow, .next-interview-arrow, .close-interview-circle  {
    display:block;width:40px;height:40px;background-repeat:no-repeat;z-index:11;background-size:cover;
}
.prev-circle-arrow {
    background-image:url('data:img/png;base64,');
}
.next-circle-arrow {
    background-image:url('data:img/png;base64,');
}

.prev-circle-arrow:hover, .prev-interview-arrow {
    background-image:url('data:img/png;base64,');
}
.next-circle-arrow:hover, .next-interview-arrow {
    background-image:url('data:img/png;base64,');
}
.close-interview-circle {
    background-image:url('data:img/png;base64,');
}    
.padding50 {padding:rem(25px) 0;}
.margin50 {margin:rem(25px) 0;}
